import React, { useState, useEffect } from 'react';
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';
import { useNavigate } from 'react-router-dom';

function CalendarComponent() {
  const [tasks, setTasks] = useState([]); // 
  const [workdays, setWorkdays] = useState([]); // 
  const [newTask, setNewTask] = useState(''); // 
  const [selectedDate, setSelectedDate] = useState(null); // 

  const navigate = useNavigate();  //  useNavigate 

  //  localStorage 
  useEffect(() => {
    const savedTasks = JSON.parse(localStorage.getItem('tasks')) || [];
    const savedWorkdays = JSON.parse(localStorage.getItem('workdays')) || [];
    setTasks(savedTasks);
    setWorkdays(savedWorkdays);
  }, []);

  //  localStorage
  useEffect(() => {
    localStorage.setItem('tasks', JSON.stringify(tasks));
    localStorage.setItem('workdays', JSON.stringify(workdays));
  }, [tasks, workdays]);

  // 
  const toggleTaskStatus = (taskId) => {
    setTasks(
      tasks.map((task) =>
        task.id === taskId
          ? {
              ...task,
              status: task.status === '' ? '' : '',
              className: task.status === '' ? 'button-completed' : 'button-pending',
            }
          : task
      )
    );
  };

  // 
  const handleWorkdaySelection = (date) => {
    const dateString = date.toDateString();
    if (workdays.includes(dateString)) {
      setWorkdays(workdays.filter((day) => day !== dateString)); // 
    } else {
      setWorkdays([...workdays, dateString]); // 
    }
  };

  // 
  const handleTaskInput = (e) => {
    setNewTask(e.target.value);
  };

  // 
  const addNewTask = () => {
    const newTaskObj = {
      id: new Date().getTime(), //  id
      date: selectedDate.toDateString(),
      task: newTask,
      status: '',
      className: 'button-pending',
    };
    setTasks([...tasks, newTaskObj]);
    setNewTask(''); // 
  };

  // 
  const renderTasksForSelectedDate = () => {
    if (!selectedDate) return [];
    return tasks.filter((task) => task.date === selectedDate.toDateString());
  };

  const goHome = () => {
    navigate('/HomePage');  // 
  };

  return (
    <div className="calendar-container content-section">
      <h2 className="major"></h2>
      <div className="instructions">
        <p></p>
        <ol>
          <li></li>
          <li></li>
          <li></li>
        </ol>
      </div>

      {/*  */}
      <div className="workday">
        <h3></h3>
        <Calendar
          onClickDay={(date) => {
            setSelectedDate(date); // 
            handleWorkdaySelection(date); // 
          }}
          tileClassName={({ date }) => {
            const isWorkday = workdays.includes(date.toDateString());
            const isSelected = selectedDate && selectedDate.toDateString() === date.toDateString();
            return isSelected
              ? 'selected-day' // 
              : isWorkday
              ? 'workday' // 
              : null;
          }}
        />
      </div>

      {/*  */}
      <div className="todo-section">
        <h3></h3>
        {/*  */}
        <div className="form-group">
          <input
            type="text"
            className="form-control"
            value={newTask}
            onChange={handleTaskInput}
            placeholder=""
          />
          <button
            className="button-primary"
            onClick={addNewTask}
            disabled={!selectedDate || !newTask} // 
          >
            
          </button>
        </div>

        {/*  */}
        <div className="table-wrapper">
          <table className="table">
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {renderTasksForSelectedDate().map((task) => (
                <tr key={task.id}>
                  <td>{task.date}</td>
                  <td>{task.task}</td>
                  <td>
                    <button
                      className={task.className}
                      onClick={() => toggleTaskStatus(task.id)} //  task.id 
                    >
                      {task.status}
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {/*  */}
      <button onClick={goHome}></button>
    </div>
  );
}

export default CalendarComponent;

